import styles from '../styles/modules/results.module.scss'
import { useSelector } from 'react-redux'
// import { useEffect, useState } from 'react'

const formatDate = (date) => {
  const MONTH_NAMES = [
    `–Ø–Ω–≤–∞—Ä—å`,
    `–§–µ–≤—Ä–∞–ª—å`,
    `–ú–∞—Ä—Ç`,
    `–ê–ø—Ä–µ–ª—å`,
    `–ú–∞–π`,
    `–ò—é–Ω—å`,
    `–ò—é–ª—å`,
    `–ê–≤–≥—É—Å—Ç`,
    `–°–µ–Ω—Ç—è–±—Ä—å`,
    `–û–∫—Ç—è–±—Ä—å`,
    `–ù–æ—è–±—Ä—å`,
    `–î–µ–∫–∞–±—Ä—å`,
  ]

  return `${MONTH_NAMES[date.getMonth()]}
    ${date.getDate()},
    ${date.getFullYear()},
    ${date.getHours()}:
    ${date.getMinutes()}`
}

const formatTime = (millisec) => {
  const normalizeTime = (time) =>
    time.length === 1 ? time.padStart(2, '0') : time

  let seconds = (millisec / 1000).toFixed(0)
  let minutes = Math.floor(parseInt(seconds) / 60).toString()
  let hours = ''

  if (parseInt(minutes) > 59) {
    hours = normalizeTime(Math.floor(parseInt(minutes) / 60).toString())
    minutes = normalizeTime(
      (parseInt(minutes) - parseInt(hours) * 60).toString()
    )
  }
  seconds = normalizeTime(Math.floor(parseInt(seconds) % 60).toString())

  if (hours !== '') {
    return `${hours} —á. ${minutes} –º–∏–Ω. ${seconds} —Å–µ–∫.`
  }
  return `${minutes} –º–∏–Ω. ${seconds} —Å–µ–∫.`
}

const getColor = (correctAnswer, userAnswer) => {
  let color = '#326799'
  if (correctAnswer) {
    return (color = 'green')
  }
  if (userAnswer && !correctAnswer) {
    return (color = '#d63f3f')
  }
  return color
}

const ViewAnswersUser = () => {
  const { users, questions, currentUserId } = useSelector((state) => state)

  const currentUser = users.find((it) => it._id === currentUserId)
  // console.log(currentUser)

  return (
    <section className={styles.results}>
      {currentUser && (
        <>
          <h2 className={styles.title}>{currentUser.user}</h2>
          <div className={styles.table__wrap}>
            <div className={styles.info}>
              <div>
                <span>–≠–ª–µ–∫—Ç—Ä–æ–Ω–Ω–∞—è –ø–æ—á—Ç–∞: </span>
                <span>{currentUser.email}</span>
              </div>
              <div>
                <span>–ü—Ä–∏—Å—Ç—É–ø–∏–ª –∫ —Ç–µ—Å—Ç—É: </span>
                <span>{currentUser.isTestingStarted ? '–î–∞' : '–ù–µ—Ç'}</span>
              </div>
              {currentUser.endTime ? (
                <div>
                  <span>–¢–µ—Å—Ç –∑–∞–∫–æ–Ω—á–µ–Ω: </span>
                  <span>{formatDate(new Date(currentUser.endTime))}</span>
                </div>
              ) : null}
              {currentUser.differenceTime ? (
                <div>
                  <span>–ù–∞ —Ç–µ—Å—Ç –ø–æ—Ç—Ä–∞—á–µ–Ω–æ: </span>
                  <span>{formatTime(currentUser.differenceTime)}</span>
                </div>
              ) : null}
            </div>
            {currentUser.tests.length !== 0 ? (
              <ul className={styles.questions}>
                {currentUser.tests.map((it, i) => {
                  const currentCorrectQuestion = questions[i]
                  return (
                    <li key={it._id}>
                      <span>{it.question}</span>
                      <ul>
                        {it.answers.map((it, i) => {
                          const currentCorrectAnswer =
                            currentCorrectQuestion?.answers[i].isTrue
                          const currentUserAnswer = it.isTrue
                          return (
                            <li
                              style={{
                                color: getColor(
                                  currentCorrectAnswer,
                                  currentUserAnswer
                                ),
                              }}
                              key={it._id}
                            >
                              {it.answer}
                            </li>
                          )
                        })}
                      </ul>
                    </li>
                  )
                })}
              </ul>
            ) : null}
          </div>
        </>
      )}
    </section>
  )
}

export default ViewAnswersUser
