import { useState } from 'react'
import Loader from './loader'
import styles from '../styles/modules/questions.module.scss'
import okIcon from '../img/ok.svg'
import { useDispatch, useSelector } from 'react-redux'
import { useHttp } from '../hooks/http.hooks'
import { setIsSent } from '../reducer/reducer'

const initPlaceholder = `–ú–æ–∂–Ω–æ –ª–∏ —Å–∫—Ä–∏–ø—Ç–æ–º –ø–µ—Ä–µ–≤–µ—Å—Ç–∏ –ø–æ—Å–µ—Ç–∏—Ç–µ–ª—è –Ω–∞ –¥—Ä—É–≥—É—é —Å—Ç—Ä–∞–Ω–∏—Ü—É —Å–∞–π—Ç–∞?
+–î–∞, –∫—É–¥–∞ —É–≥–æ–¥–Ω–æ.
-–î–∞, –Ω–æ —Ç–æ–ª—å–∫–æ –≤ —Ä–∞–º–∫–∞—Ö —Ç–µ–∫—É—â–µ–≥–æ —Å–∞–π—Ç–∞.
-–ù–µ—Ç, –Ω–µ–ª—å–∑—è.
*
–ö–∞–∫–æ–µ —Å–æ–±—ã—Ç–∏–µ –∏–∑ —ç—Ç–æ–≥–æ —Å–ø–∏—Å–∫–∞ –Ω–µ —Å—É—â–µ—Å—Ç–≤—É–µ—Ç?
+onmousescroll
-onclick
-onmouseover
-onmousemove
-onwheel
-–≤—Å–µ —Å—É—â–µ—Å—Ç–≤—É—é—Ç.`

const createQuestion = (data) => {
  let question = {
    question: data[0],
    answers: [],
  }

  for (let i = 1; i < data.length; i++) {
    let isTrue

    if (data[i][0] === '+') {
      isTrue = true
    } else {
      isTrue = false
    }

    let answer = data[i].slice(1)

    question.answers.push({
      answer,
      isTrue,
    })
  }

  return question
}

const Questions = () => {
  const dispatch = useDispatch()
  const { questions, isSent } = useSelector((state) => state)
  const [newQuestions, setNewQuestions] = useState('')
  const { request } = useHttp()

  const placeholder =
    questions.length === 0
      ? initPlaceholder
      : questions.map((it) => it.question).join('\n')

  const onSubmit = async () => {
    dispatch(setIsSent(true))
    const structuredQuestions = newQuestions
      .split('*')
      .map((it) => it.split('\n'))
      .map((it) => it.filter((it) => it !== ''))
      .map((it) => createQuestion(it))
    try {
      await request(
        '/api/questions/add',
        'POST',
        { structuredQuestions },
        {
          Authorization: `Bearer ${localStorage.token}`,
        }
      )
      setNewQuestions('')
      dispatch(setIsSent(false))
    } catch (err) {
      console.log(err)
      dispatch(setIsSent(false))
    }
  }

  return (
    <section>
      {isSent ? <Loader /> : null}
      <h2 className={styles.title_questions}>—Å–ø–∏—Å–æ–∫ –≤–æ–ø—Ä–æ—Å–æ–≤</h2>
      <section className={styles.questions}>
        <span onClick={onSubmit}>
          –ó–∞–≥—Ä—É–∑–∏—Ç—å –Ω–∞ —Å–µ—Ä–≤–µ—Ä
          <img src={okIcon} alt={'question'} />
        </span>
        <textarea
          placeholder={placeholder}
          rows='25'
          value={newQuestions}
          onChange={(e) => {
            setNewQuestions(String(e.target.value))
          }}
        />
      </section>
    </section>
  )
}

export default Questions
